import React, { Component } from 'react'
import { Tabs } from 'antd'
import { compose } from 'redux';
import { connect } from 'react-redux'
import { firestoreConnect, withFirestore } from 'react-redux-firebase';
import ItineraryTable from './ItineraryTable'

const { TabPane } = Tabs;

class Profile extends Component {
  constructor(props) {
    super(props)
    this.state = {
      itineraries: {}
    }
  }

  componentDidUpdate(prevProps) {

  }

  render() {
    console.log(this.props)
    return (
      <div>
        <ItineraryTable></ItineraryTable>
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  let itineraries = state.firestore.data['users/' + state.firebase.auth.uid + '/itineraries'];
  return {
    ...ownProps,
    auth: state.firebase.auth,
    itineraries: itineraries,
    lastItinerary: itineraries ? itineraries[Object.keys(itineraries).length - 1] : null
  }
}

export default compose(
  withFirestore,
  connect(mapStateToProps, null),
  firestoreConnect(props => [{
    collection: 'users/' + props.auth.uid + '/itineraries',
    limit: 10
  }])
)(Profile)